@use '../scss/index' as *;

:root{
    @each $color, $value in $colors{
        --clr-#{$color}: #{$value};
    }
}

//Typo stuff
@each $level in $type-levels{
    $level-size: $level-size * $type-size-ratio;;

    h#{$level}{
        display: inline;
        color: white;
        font-weight: $semi-bold;
        font-family: $poppins;
        font-size: clamp(
            #{$fluid-min}rem,
            #{$fluid-scaler} + 1rem,
            #{$level-size}
        );

    }
}



///General stuff
html,body{
    margin: 0;
    padding: 0;
    
}

body{
    background-color: var(--clr-#{background-bottom});
    
}

.menu{
    @include flex-pos(flex-end, '', '');
    width: 100%;
}

nav.primary--nav{
    
    @include flex-pos(space-around, center, 0em);
    flex-flow: $column-wrap;
    position: absolute;
    z-index: 3000;

    height: $full-height;
    width: 7em;

    box-sizing: border-box;

    div.nav--links{
        //Variables
        $pad: 1.5rem;
        $blur: 1rem;
        $durantion: 900ms;
        $easing: cubic-bezier(0.56, -0.03, 0.42, 1.06);
        transform-origin: top;

        @include flex-pos(space-around, center, 2rem);
        padding-top: 4.5rem;
        padding-bottom: $pad;
        flex-flow: $column-wrap;

        height: 100%;
        width: 100%;

        box-sizing: border-box;
        backdrop-filter: blur($blur);
        transform: scaleY(0);
        background-color: rgba(black, 0.3);

        transition: $durantion $easing
    }

    a.nav__link{
        box-sizing: border-box;
        @include flex-pos(center, center, none);
        transform: translateX(20%);
        opacity: 0;
        flex-flow: row wrap;

        width: 4rem;
        aspect-ratio: 1;
        text-decoration: none;
        font-family: $poppins;
        font-style: $regular;
        font-weight: $bold;


    }
}

.hamburguer--button{
    $margin-elements: 0.5rem;
    $durantion: 280ms;
    $easing: cubic-bezier(0.79, 0.14, 0.31, 1.19);
    
    @include flex-pos(center, center,'');

    width: 2.5rem;
    padding: .6em;
    aspect-ratio: 1;
    position: absolute;
    z-index: 3000;
    top: 0.9rem;

    &::before, &::after{
        @include pseudo-element('', block,  3rem, 2px);

        background-color: var(--clr-#{default-purple});
        transition: $durantion $easing;
    }

    &::before{
        margin: -$margin-elements 0rem 0rem 0rem;

    }

    &::after{
        margin: $margin-elements 0rem 0rem 0rem;

    }
}

.primary--nav[data-visible='true']{
    div.nav--links{

        transform: scaleY(1);

        a{
            transition: 600ms cubic-bezier(0.89, 0.01, 0.23, 0.63);
            transition-delay: 2s;
            opacity: 1;
            transform: translateX(0%);
        }
    }

    .hamburguer--button::before{
        margin: 0rem;
        transform: rotate(-45deg);
    }

    .hamburguer--button::after{
        margin: 0rem;
        transform: rotate(-135deg);
    }
}



.nav__link {
    
    &:hover{
        svg{
            display: none;
        }
        h4{
            display: inline;
        }
    }

    h4{
        @include fade-in(280ms, ease-out, 20px, 0px);
        display: none;
        color: var(--clr-#{default-purple});
        text-align: center;
        font-weight: $bold;

    }

    svg{
        width: 2.25rem;
        aspect-ratio: 1;

        color: white;

    }
}